@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.logo-container {
    width: 100%;
    height: auto;
    height: 25vh;
    overflow: hidden;
}

img {
    max-width: 100%;
    height: 100%;
}
.nav-container {
    width: 100%;
    background-color: #B63D5F;
    height: 7vh;
    display: flex;
    justify-content: center;
}

.nav-container button {
    color: white;
    background-color: #B63D5F;
    padding: 0;
    margin: 0;
    height: 7vh;
    padding: 0 1rem;
    width: 12vw;
    border: none;
    font-family: 'Oswald', sans-serif;
    font-size: 1.25rem;
    transition: all 0.5s;
}

.nav-container button:hover {
    background-color: white;
    color: #B63D5F;
}


#admin-btn {
    position: absolute;
    right: 0;
    color: #ffb4c9;
}


.home-container {
    height: 61vh;
    width: 100%;
    background-image: url(/js-react-restaurant-app/static/media/pizza-background.3ab2a02a.jpeg);
    background-size:cover;
    background-repeat: no-repeat;
}

.menu-container{
    display: flex;
    background-image: url(/js-react-restaurant-app/static/media/menu-background.b7ab0640.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
}
.menu-list-container {
    width: 50%;
    margin-left: 3rem;

}

.menu-list-container h2 {
    color: white;
    font-family: 'lobster', cursive;
    font-size: 3rem;
}

/* .menu-list {
    overflow: scroll;
    height: 60rem;
    direction: rtl;
} */



.menu-item-container {
    border: 3px solid white;
    margin: 1rem auto;
    padding: 1rem;
    width: 550px;
    color: white;
    background-color: #B63D5F;

}

.menu-button {
    border: 2px solid white;
    color:  white;
    font-weight: 700;
    padding: 0.65rem 1.15rem;
    background-color:  #B63D5F;
    transition: color 1s, background-color 1s;
}

.menu-button:hover {
    color:  #B63D5F;;
    background-color: white;

}

.order-list-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 3rem;
}
.order-list-container h2 {
    color: white;
    font-family: 'lobster', cursive;
    font-size: 3rem;
}


.order {
    position: sticky;
    top: 40px;
    margin-top: 0.95rem;
    background-color: white;
    color: #B63D5F;
    border: 3px solid #B63D5F;
    width: 60%;
    padding: 1.5rem;
    font-size: 0.85rem;
}

.total-continue-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 0rem 1rem;
}

.total-continue-container h3 {
    font-family: "lobster";
    font-size: 1.5rem;
    margin: 0;
}


.total-tax, .total-amount {
    display: flex;
    width: 40%;
    justify-content: space-between;
    align-items: center;
}

.total-tax p, .total-amount p {
    color: black;
    font-weight: 700;
}

.continue-button-container {
    margin-top: 1rem;
}

.continue-order-button {
    background-color: white;
    border: 2px solid #B63D5F;
    color: #B63D5F;
    width: 175px;
    padding: 0.25rem;
    font-family: "open sans", sans-serif;
    transition: all 0.5s;
}

.continue-order-button:hover {
    color: white;
    background-color: #B63D5F;
    letter-spacing: 1px;
}


.order-item {
    display: flex;
    justify-content: space-between;
    padding: 0rem 1rem;
    color: #B63D5F;

}

.order-item h3, .order-item p {
    padding: 0;
    margin: 0;
}

.order-item h3 {
    text-align: left;
    margin-bottom: 1.5rem;
}
.aboutus-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 61vh;
    width: 100%;
    background-image: url(/js-react-restaurant-app/static/media/aboutus-background.3af88d5b.jpeg);
    background-position: 50% 50%;
}

.aboutus-blurb {
    width: 50vh;
    background-color: white;
    padding: 0rem 1rem 0.5rem 1rem;
    border: 10px solid #B63D5F;

}

.aboutus-blurb h2 {
    color: #B63D5F;   
    font-size: 1.75rem;
    font-family: 'lobster', cursive;
}

.aboutus-blurb p {
    font-family: 'Open Sans', sans-serif;
    line-height: 1.75rem;
}

.contactus-container {
    background-image: url(/js-react-restaurant-app/static/media/contactus-background.da247d17.jpeg);
    height: 61vh;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 75% 70%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-card {
    width: 200px;
    background-color: white;
    padding: 0rem 1rem 1rem 1rem;
    border: 10px solid #B63D5F;
    font-family: 'open sans', sans-serif;

}

.contact-card h2 {
    font-family: 'lobster', sans-serif;
    color: #B63D5F;
}
.contact-card a{
    font-weight: 700;
}

h1{ 
    margin-top: 3rem;
}
.admin-order-card {
    border: 5px solid #B63D5F;;
    width: 50%;
    margin: 3rem auto;
    padding: 1rem
}

.admin-order-card li {
    position: relative;
    list-style-type: none;
    right: 20px;
}

.admin-view-inputs-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.admin-view-inputs-container button {
    width: 100px;
    margin: 0.75rem 0rem;
    padding: 0.25rem 0.5rem;
}


.footer-container {
    height: 7vh;
    background-color: #B63D5F;
}

.order-submit-container {
    background-image: url(/js-react-restaurant-app/static/media/submit-background.8e3e9966.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 61vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.order-submit-card{
    width: 30%;
    height: 300px;
    overflow: scroll;
    text-align: left;
    background-color: white;
    padding: 0rem 1rem 0.5rem 1rem;
    border: 10px solid #B63D5F;
}

.order-submit-card h2 {
    color: #B63D5F;
    font-family: 'lobster', cursive;

}

.order-submit-card button {
    background-color: white;
    border: 2px solid #B63D5F;
    color: #B63D5F;
    padding: 0.25rem 0.5rem;
    font-family: "open sans", sans-serif;
    transition: all 0.5s;

}

.order-submit-card button:hover {
    color: white;
    background-color: #B63D5F;
    letter-spacing: 1px;

}

.order-item-list {
    margin-top: 1.5rem;
    margin-bottom: 2.5rem;
}

.order-item-final {
    display: flex;
    justify-content: space-between;
    padding: 0;
}

.total-final {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 1rem;
    margin-bottom: 1.5rem;
}

.total-final h2, .total-final p {
    padding: 0;
    margin: 0;
}

.total-final h2 {
    margin-right: 1rem;
}

.total-final p {
    color: green;
}

.tax-total-final {
    display: flex;
    width: 150px;
    justify-content: space-between;
    align-items: center;
}

.submit {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

#full-name {
    width: 100%;
    margin-right: 1.5rem;
}

#submit-order {
    width: 8rem;
}
